 $container-padding-horizontal: 40;
$container-width: 829;

.users-list {
  padding: 72px #{$container-padding-horizontal}px 48px;
  max-width: 100vw;
}

.users-list-title {
  font-size: 28px;
  line-height: 34px;
  color: var(--color-black);
  margin-bottom: 16px;
  font-weight: 500;
}

.users-list-subtitle {
  color: var(--color-secondary-text);
  margin-bottom: 48px;
}

.users-list-paper {
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.16);
  border-radius: 6px;
  background-color: var(--color-white);
  display: block;
  width: 100%;
  max-width: 100%;
}

.users-list .block-info-connection {
  margin-bottom: 24px;
}

@media only screen and (min-width: #{$container-width + 2 * $container-padding-horizontal}px) {
  .users-list { 
    width: #{$container-width}px;
    padding-left: 0px;
    padding-right: 0px;
    margin: 0 auto;
  }
}
